
.checkup{
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0 30px;
}
.checkup-button{
  background:#E67E22;
  border: none;
  -webkit-transition: all 240ms ease;
  -o-transition: all 240ms ease;
  transition: all 240ms ease;
  width:130px;
  border-radius: 0px 4px 4px 0px;
  color:#fff;
  font-size: 20px;
}
.checkup-input{
  border-radius:4px 0px 0px 4px;
  border: 1px Solid #ccc;
  padding:15px;
  font-size: 20px;
  width: 600px;
}
.checkup-input:focus{
  border:2px solid #e67e22;
  outline: none
}
.checkup-form{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height:70px;
}
.checkup-analysis{
  color: #555;
  font-weight: 400;
  font-size: 25px;
}
.checkup h1{
	margin-bottom: 0;
}


@media all and (min-width: 600px){
	.checkup h1{
	  font-size: 40px
  }
}


