body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.Search{
  height: 100vh;
  display: inline-block;
  background: -webkit-gradient(linear,left top, left bottom,from(rgba(255,255,255,0.8)),to(rgba(255,255,255,0.8))),url('./images/bg-1.jpg');
  background: -webkit-linear-gradient(rgba(255,255,255,0.8),rgba(255,255,255,0.8)),url('./images/bg-1.jpg');
  background: -o-linear-gradient(rgba(255,255,255,0.8),rgba(255,255,255,0.8)),url('./images/bg-1.jpg');
  background: linear-gradient(rgba(255,255,255,0.8),rgba(255,255,255,0.8)),url('./images/bg-1.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  width:100%;
  position: relative;

}



